import { Component } from '@angular/core';
import { NavController, NavParams, AlertController } from 'ionic-angular';

import { FoodPage } from './food';
import { MeetingPage } from './meeting';
import { WeddingPage } from './wedding';

import { ProductService } from '../../providers/product';
import * as $ from 'jquery';


@Component({
	selector: 'page-room',
	templateUrl: 'room.html',
	providers: [ProductService]
})
export class RoomPage {
	public account_id = localStorage.getItem('account_id');
	public admin = true;
	public showedit = false;

	public menulists = [];
	public addname = '';
	public productlist = [];
	public cartlist = [];

	public showcart = false;

	public modalVisible = false;
	public placeForm = {};

	public secondname;       //选中的分类名称
	public typeP_num = 0;	//选中的分类包含商品数量
	public typeId;

	public imgUpLoad;  //上传图片

	constructor(
		public navCtrl: NavController,
		public navParams: NavParams,
		public service: ProductService,
		public alertCtrl: AlertController
	) {
		this.getproducts();
	}

	checkMenu(id, name) {
		this.secondname = name;
		this.showedit = false;
		this.typeId = id;

		this.productlist = [];
		
		this.service.getProductByType(id).then(data => {
			if(data.status==1){
				for (let i = 0; i < data['list'].length; i++) {
					let t = {
						id: data['list'][i]['id'],
						img: data['list'][i]['ref_pic_url'],
						title: data['list'][i]['product_name'],
						detail: data['list'][i]['description'],
						price: data['list'][i]['price']
					};
					this.productlist.push(t);
				};
				this.typeP_num = data['list'].length;
			}
		});
	}

	addMenu() {
		// this.menulists.push(this.addname);
		let query = {
			name: this.addname
		}
		this.service.AddRoomType(query).then(data => {
			if (data) {
				//this.getMemu();
			}
		});
		this.addname = '';
	}

	getproducts() {
		this.service.getRoomMemu(this.account_id).then(data => {
			if(data.status==1){
				for (let i = 0; i < data['product'].length; i++) {
					let t = {
						id: data['product'][i]['id'],
						img: data['product'][i]['ref_pic_url'],
						title: data['product'][i]['product_name'],
						detail: data['product'][i]['description'],
						price: data['product'][i]['price'],
					};
					this.productlist.push(t);
				};	
				for (let i = 0; i < data['list'].length; i++) {
					let t = {
						img : data['list'][i]['img'],
						name : data['list'][i]['name'],
						id : data['list'][i]['id']
					}
					this.menulists.push(t);
				};

				this.secondname = data['list'][0]['name'];
				this.typeP_num = data['product'].length;
				this.typeId = data['list'][0]['id'];
			}
		});
	}

	editProducts() {
		this.showedit = true;
	}

	editSucess() {
		this.showedit = false;
	}
	// 删除产品
	delItem(id) {
		let alert = this.alertCtrl.create({
			title: '确认删除？',
			// subTitle: 'Your friend, Obi wan Kenobi, just accepted your friend request!',
			buttons: ['确定']
		});
		alert.present();
	}
	// 新增产品
	addProducts() {
		this.placeForm = {};
		this.modalVisible = true;
		this.getproducts();
	}

	// 修改产品
	editItem(id) {
		this.placeForm = {
			name: '产品名称',
			price: '1998',
			detail: '产品详细介绍'
		}
		this.modalVisible = true;
	}

	formSubmit(form) {
		form.img = this.imgUpLoad;
		this.service.addProduct(this.account_id, this.typeId, form.name, form.price, form.cost, form.unit, form.detail, form.img).then(data => {
			this.modalVisible = false;
		})
	}

	hideModal() {
		this.modalVisible = false;
	}

	// 清空购物车
	clearCart() {

	}

	//加入购物车
	addCart(id) {
		console.log(id);
	}

	showCart() {
		this.showcart = true;
		this.getCartlist();
	}

	hideCart() {
		this.showcart = false;
	}

	getCartlist() {
		this.cartlist = [
			{
				id: '1',
				title: '988婚宴',
				price: '100',
				amount: '12',
				mark: '12小时使用时长，包含免费摆台，免费茶水'
			},
			{
				id: '2',
				title: '988婚宴',
				price: '100',
				amount: '12',
				mark: '12小时使用时长，包含免费摆台，免费茶水'
			},
			{
				id: '3',
				title: '988婚宴',
				price: '100',
				amount: '12',
				mark: '12小时使用时长，包含免费摆台，免费茶水'
			},
		];
	}

	gotoMenu(name) {
		console.log(name);
		if (name == 'meeting') {
			this.navCtrl.push(MeetingPage).then(() => {
				this.navCtrl.removeView(this.navCtrl.getPrevious());
			});
		} else if (name == 'food') {
			this.navCtrl.push(FoodPage).then(() => {
				this.navCtrl.removeView(this.navCtrl.getPrevious());
			});;
		} else if (name == 'room') {
			// this.navCtrl.push(RoomPage).then(() => {
			// 	this.navCtrl.removeView(this.navCtrl.getPrevious());
			// });;
		} else if (name == 'wedding') {
			this.navCtrl.push(WeddingPage).then(() => {
				this.navCtrl.removeView(this.navCtrl.getPrevious());
			});;
		} else {
			console.log("找不到此页面");
		}
	}

	// 上传图片传回base64
	public uploadfn2(data) {
		this.imgUpLoad = data;
	}
}



