import { Component } from '@angular/core';
import { NavController, NavParams, } from 'ionic-angular';

import { ToastController } from 'ionic-angular';

import * as echarts from 'echarts';
import { ChartService } from '../../providers/chart';

//import { FileChooser } from '@ionic-native/file-chooser';


@Component({
	selector: 'page-finance',
	templateUrl: 'finance.html',
	providers: [ChartService]
})

export class financeChartPage {
	public pricelists = [];
	public goalslists = [];
	public agginAllData = {};
	public proCostlist = [];
	public proAllData = {};
	public proCurrentId;

	public modalVisible = false;   // 业绩分配Modal
	public setboxVisible = false;  // 设置业绩分配Modal
	public costVisible = false;    // 执行成本Modal
	public setCostVisible = false; // 设置执行成本Modal

	public ifNew = true; //是否显示添加项目按钮

	public totalA = {
		profit: 0,
		receive: 0,
		money: 0,
	};
	public proCommon = [];

	public allStaff = [];
	public allPercent = [];
	public selectStaff = [];


	constructor(
		public navCtrl: NavController,
		public navParams: NavParams,
		public toastCtrl: ToastController,
		public service: ChartService,
	) {
		this.getPricelists();
	}
	getPricelists() {
		this.service.getPricelist().then(data => {
			if (data.status == 1) {
				this.totalA.profit = data.yearTotalGrossProfit;
				this.totalA.receive = data.yearTotalActualReceivedMoney;
				this.totalA.money = data.yearTotalOrderMoney;
				this.pricelists = data.orders;
			}
		});
	}

	getGoalsList(id) {
		this.service.getGoallist(id).then(data => {
			if (data.status == 1) {
				this.agginAllData = data;
				this.goalslists = data.project_participant;
				console.log(this.goalslists);
				this.goalslists.map((item, index) => {
					this.allPercent[index] = item.commission;
					this.selectStaff[index] = item.staff_id;
				});

				//虚拟数据===所有工作人员列表{}
				this.allStaff = [
					{
						id: 1,
						name: '小鱼1'
					},
					{
						id: 2,
						name: '小鱼2'
					}, {
						id: 3,
						name: '小鱼3'
					}, {
						id: 4,
						name: '小鱼4'
					},
				];
				console.log(this.selectStaff);
				console.log(this.allPercent);
				console.log(this.goalslists);
			}
		});

	}

	// 获取执行成本
	getproCostlist(id) {
		this.service.getProCost(id).then(data => {
			if (data.status == 1) {
				this.proAllData = data;
				this.proCostlist = data.order_supplier_payment;
				this.proCommon = data.order[0];
			}
		});

	}

	showAssign(id) {
		this.modalVisible = true;
		this.getGoalsList(id);
	}
	hideModal() {
		this.modalVisible = false;
	}

	formSubmit(form) {
		console.log(form);
		let total = 0;
		for (var Key in form) {
			console.log(form[Key]);
			total = total + parseFloat(form[Key]);
		}
		console.log(total);
		if (total != 100) {
			let toast = this.toastCtrl.create({
				message: '分配总和不等于100,请重新分配',
				showCloseButton: true,
				position: 'middle',
			});
			toast.present();
		} else {
			this.hidesetModal();
		}
		// form.map((item, index)=>{
		// 	console.log(item);
		// 	total = total + item;
		// })
		// this.hidesetModal();
	}

	// formStaffSubmit(form) {
	// 	console.log(form);
	// }

	submitPercent(value: any, id) {
		// 提交百分比设置
		console.log(id);
		console.log(value);
		let query = {
			id: id,
			commission: value
		}
		this.service.editDistribution(query).then(data => {
			if (data.status == 1) {
				console.log("提交百分比成功");
			}
		});
	}
	submitStaff(value: any, id) {
		// 提交员工设置
		console.log(id);
		console.log(value);
		let query = {
			id: id,
			staff_id: value
		}
		this.service.editDistribution(query).then(data => {
			if (data.status == 1) {
				console.log("提交员工成功");
			}
		});
	}

	editGoals() {
		this.setboxVisible = true;
	}
	hidesetModal() {
		this.setboxVisible = false;
	}

	delsetItem() {
		// 删除分配项目
	}

	showCostMoal(id) {
		this.costVisible = true;
		this.proCurrentId = id;
		this.getproCostlist(id);
		console.log(this.proCostlist);
	}
	hideCostMoal() {
		this.costVisible = false;
	}

	formCostSubmit(form, id) {
		form.order_id = this.proCurrentId;
		form.pay_time = new Date();
		this.service.formCost(form).then(data => {
			if (data.status == 1) {
				this.hidesetcostModal();
				this.getproCostlist(this.proCurrentId);
			}
		});
	}

	showsetCostMoal(id) {
		this.setCostVisible = true;
	}

	hidesetcostModal() {
		this.setCostVisible = false;
	}

	addProName() {
		// 添加项目
		this.ifNew = false;
	}
	finishAdd() {
		// 完成项目
		this.ifNew = true;
	}

}



