import { query } from '@angular/core/src/animation/dsl';
import { Component } from '@angular/core';
import { NavController, NavParams, AlertController  } from 'ionic-angular';
import { HallService } from '../../providers/hall';


@Component({
	selector: 'page-piscs',
	templateUrl: 'pics.html',
	providers: [HallService]
})
export class PicsPage {
	public spaceId=null;
	public hallId=null;
	public hallname = '';
	public piclist = [];
	public nowImg = '';
	public showBig = false;

	constructor(
		public navCtrl: NavController,
		public navParams: NavParams,
		public alertCtrl: AlertController,
		public service: HallService,
	) {
		this.spaceId = this.navParams.get('spaceId');
		this.hallId = this.navParams.get('hallId');
		this.hallname = this.navParams.get('hallname');
		this.getPics();
	}

	public ionViewWillEnter() {
        this.getPics();
		console.log(this.piclist);
    }

	getPics() {
		// this.piclist = [
		// 	{
		// 		name: '北京浩瀚一方互联网科技有限责任公司年会',
		// 		img_url: 'assets/imgs/index/bg.jpg',
		// 		update_time: '2018.01.10'
		// 	}
		// ];
		let query={
			space_id:this.spaceId,
			hall_id:this.hallId
		};
		this.service.getHallImgs(query).then(data => {
			console.log(data);
		   if(data.status==1){
			this.piclist=data.imgs;
		   }
	   });
	}

	showBigImg(img) {
		this.nowImg = img;
		this.showBig = true;
	}
	hideBigImg() {
		this.showBig = false;
	}
}



