import { Component, Input } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';

import { MeetingPage } from '../../pages/products/meeting';
import { RoomPage } from '../../pages/products/room';
import { FoodPage } from '../../pages/products/food';
import { WeddingPage } from '../../pages/products/wedding';


@Component({
	selector: '.xmb-menu',
	templateUrl: 'menu.html',
})
export class MenuDire {

	public active = '';

	constructor(
		public navCtrl: NavController,
		public navParams: NavParams
	) {
	}

	@Input()
	set preload(args) {
		this.active = args.active;
	}

	setactive() {
		switch (this.active) {
			case 'meeeting':
				console.log('meeting');
				break;
			case 'food':
				console.log('food');
				break;
			case 'room':
				console.log('room');
				break;
			case 'wedding':
				console.log('wedding');
				break;
			default:
				break;
		}
	}

	gotoMenu(name) {
		console.log(name);
		if(name == this.active){
			return;
		}else{
			this.active = name;
		}

		if (name == 'meeting') {
			this.navCtrl.push(MeetingPage,null,{animate:false}).then(() => {
				this.navCtrl.removeView(this.navCtrl.getPrevious());
			});
		} else if (name == 'food') {
			this.navCtrl.push(FoodPage,null,{animate:false}).then(() => {
				this.navCtrl.removeView(this.navCtrl.getPrevious());
			});;
		} else if (name == 'room') {
			this.navCtrl.push(RoomPage,null,{animate:false}).then(() => {
				this.navCtrl.removeView(this.navCtrl.getPrevious());
			});;
		}else if (name == 'wedding') {
			this.navCtrl.push(WeddingPage,null,{animate:false}).then(() => {
				this.navCtrl.removeView(this.navCtrl.getPrevious());
			});;
		} else {
			console.log("找不到此页面");
		}
	}



}



