	//获取当前菜单的菜品
	getMenuDetail(menuId){
		this.service.getMenuDetail(menuId).then(data => {
			if(data.status==1){
				for (let i = 0; i < data['dishes'].length; i++) {
					let t = {
						id: data['dishes'][i]['id'],
						type: data['dishes'][i]['service_type'],
						img: data['dishes'][i]['ref_pic_url'],
						name: data['dishes'][i]['product_name'],
						remark: data['dishes'][i]['description'],
						price: data['dishes'][i]['price'],
						amount: data['dishes'][i]['dishes_count']
					};
					this.foodchecks.push(t);
					this.chooselist.push(t);
				};
			}
			this.calcTotalPrice();
		});
	}

	//全部菜品按分类筛选
	getproducts(i) {
		this.foodslist = [];
		this.service.getProductByType(i).then(data => {
			console.log(data);
			if(data.status==1){
				for (let i = 0; i < data['list'].length; i++) {
					let t = {
						id: data['list'][i]['id'],
						type: data['list'][i]['service_type'],
						img: data['list'][i]['ref_pic_url'],
						name: data['list'][i]['product_name'],
						remark: data['list'][i]['description'],
						price: data['list'][i]['price']
					}
					this.foodslist.push(t);
				};
				this.dishType=data['type'];
			}
		});
	}

