import { Component } from '@angular/core';
import { NavController, NavParams, ToastController } from 'ionic-angular';
import { Social } from './../../plugin/native';
import { query } from '@angular/core/src/animation/dsl';

// import { FoodPage } from './food';
import { MeetingPage } from './meeting';
import { RoomPage } from './room';
import { WeddingPage } from './wedding';
import { EditFoods } from './editfood';

import { ProductService } from '../../providers/product';
import * as $ from 'jquery';

@Component({
	selector: 'page-food',
	templateUrl: 'food.html',
	providers: [ProductService]
})
export class FoodPage {
	public account_id = localStorage.getItem('account_id');

	public menulists = [];
	public addname = '';
	public productlist = [];

	public menuName = '';
	public dishNum = '';

	// public cartlist = [];
	// public showcart = false;

	public chooseid = '';
	public foodtype = 0;

	public modalVisible = false;
	public placeForm = {};
	public orderlist = [];

	public imgUpLoad;  //上传图片
	public typeId;

	constructor(
		public navCtrl: NavController,
		public navParams: NavParams,
		public toastCtrl: ToastController,
		public service: ProductService,
	) {
		//this.getMemu();
		// this.getproducts();
		this.getMealMemu();
	}

	setType(num) {
		switch (num) {
			case '0':
				this.foodtype = 0;
				console.log("全部")
				break;
			case '1':
				this.foodtype = 1;
				console.log("凉菜")
				break;
			case '2':
				this.foodtype = 2;
				console.log("热菜")
				break;
			case '3':
				this.foodtype = 3;
				console.log("汤羹")
				break;
			case '4':
				this.foodtype = 4;
				console.log("主食")
				break;
			default:
				break;
		}
	}

	//获取菜单列表及第一个菜单的详情
	getMealMemu() {
		this.service.getMealMemu().then(data => {
			if(data.status==1){
				this.menuName = data['list'][0]['name'];
				this.dishNum = data['dishes'].length;

				for (let i = 0; i < data['list'].length; i++) {
					let t = {
						name : data['list'][i]['name'],
						id : data['list'][i]['id']
					}
					this.menulists.push(t);
				};
				for (let i = 0; i < data['dishes'].length; i++) {
					let t = {
						id: 1,
						img: data['dishes'][i]['ref_pic_url'],
						title: data['dishes'][i]['product_name'],
						detail: data['dishes'][i]['description'],
						price: data['dishes'][i]['price']
					}
					this.productlist.push(t);
				};	
			}
		});
	}

	//获取菜单详情
	getMenuDetail(menu_id){
		this.service.getMenuDetail(menu_id).then(data => {
			this.productlist = [];
			this.menuName = data['menu']['name'];
			this.dishNum = data['dishes'].length;
			this.chooseid = menu_id
			if(data.status==1){
				for (let i = 0; i < data['dishes'].length; i++) {
					let t = {
						id: 1,
						img: data['dishes'][i]['ref_pic_url'],
						title: data['dishes'][i]['product_name'],
						detail: data['dishes'][i]['description'],
						price: data['dishes'][i]['price']
					}
					this.productlist.push(t);
				};	
			}
		});
	}


	addMenu() {
		this.menulists.push(this.addname);
		this.addname = '';
	}

	gotoEditFood() {
		this.navCtrl.push(EditFoods, {menuId: this.chooseid});
	}

	// 删除产品
	delItem(id) {
		console.log(id);
	}

	// 加入订单
	addOrder() {
		this.placeForm = {};
		this.modalVisible = true;
		this.getOrders();
		console.log(this.modalVisible);
	}

	getOrders() {
		this.orderlist = [
			{ id: 0, title: 'type1' },
			{ id: 1, title: 'type2' },
		];
	}

	hideModal() {
		this.modalVisible = false;
	}

	formSubmit(form) {
		let toast = this.toastCtrl.create({
			message: '成功加入到订单',
			showCloseButton: true,
			position: 'middle',
		});
		form.img = this.imgUpLoad;
		this.service.addProduct(this.account_id, this.typeId, form.name, form.price, form.cost, form.unit, form.detail, form.img).then(data => {
			this.modalVisible = false;
		})
	}


	//分享
	share() {

	}

	gotoMenu(name) {
		console.log(name);
		if (name == 'meeting') {
			this.navCtrl.push(MeetingPage).then(() => {
				this.navCtrl.removeView(this.navCtrl.getPrevious());
			});
		} else if (name == 'food') {
			// this.navCtrl.push(FoodPage).then(() => {
			// 	this.navCtrl.removeView(this.navCtrl.getPrevious());
			// });;
		} else if (name == 'room') {
			this.navCtrl.push(RoomPage).then(() => {
				this.navCtrl.removeView(this.navCtrl.getPrevious());
			});;
		} else if (name == 'wedding') {
			this.navCtrl.push(WeddingPage).then(() => {
				this.navCtrl.removeView(this.navCtrl.getPrevious());
			});;
		} else {
			console.log("找不到此页面");
		}
	}

	// 上传图片传回base64
	public uploadfn2(data) {
		this.imgUpLoad = data;
	}
}



